import Vue from 'vue';
import Router from 'vue-router';
import { routers } from './router';
import store from '../store/index';
const whiteList = ['/commodityClass', '/atOrder'];
const whiteNameList = ['commpdityManage', 'commodityManageClass'];
Vue.use(Router);
const router = new Router({
    mode: 'history',
    base: '/mt',
    routes: routers
});
router.beforeEach((to, from, next) => {
    // let igNoreArr = ['paySuccess', 'orderConfirm', 'orderList', 'orderAdmin', 'commodityClass']
    // if (igNoreArr.indexOf(to.name) > -1) {
    //   next()
    // }
    // if (to.query.code || store.state.sessionId || ((to.name === 'login' || to.name === 'staffLogin') && to.query.openid)) {
    //   next()
    // }
    // whiteList.indexOf(to.path) > -1 || whiteNameList.indexOf(to.name) > -1
    if ((whiteList.indexOf(to.path) > -1 || whiteNameList.indexOf(to.name) > -1) || (store.state.sessionId && store.state.userInfo) && to.name !== 'login') {
        next();
    }
    else {
        // other pages that do not have permission to access are redirected to the login page.
        if (to.name === 'login') {
            next();
        }
        else {
            next(`/login?redirect=${to.path}`);
        }
    }
    setTitle(to.meta.title);
});
export function setTitle(title) {
    title = title || '';
    window.document.title = title;
}
export default router;
