const commodityManageRoute = [
    {
        path: '/shopList',
        name: 'shopList',
        component: () => import(/* webpackChunkName: "shopList" */ '../../views/appcenter/commodityManage/shop-list.vue'),
        meta: {
            title: '店铺管理'
        },
        children: [
            {
                path: 'shopAdd',
                name: 'shopAdd',
                component: () => import(/* webpackChunkName: "shopAdd" */ '../../views/appcenter/commodityManage/shop-add.vue'),
            },
            {
                path: 'commodityManageClass',
                name: 'commodityManageClass',
                component: () => import(/* webpackChunkName: "commodityManageClass" */ '../../views/appcenter/commodityManage/commodity-class.vue'),
                meta: {
                    title: '商品分类'
                },
                children: [
                    {
                        path: 'commodityManage',
                        name: 'commpdityManage',
                        component: () => import(/* webpackChunkName: "commodityManage" */ '../../views/appcenter/commodityManage/index.vue'),
                        meta: {
                            title: '商品管理'
                        },
                        children: [
                            {
                                path: 'commodityAdd',
                                name: 'commodityAdd',
                                component: () => import(/* webpackChunkName: "commodityAdd" */ '../../views/appcenter/commodityManage/commodity-add.vue'),
                                meta: {
                                    title: '商品详情'
                                }
                            }
                        ]
                    }
                ]
            }
        ]
    }
];
export default commodityManageRoute;
